.SmurfCard {
    width: 200px;
    height: 200px;
    padding: 5px;
    background: #eee;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s
}

.SmurfCard:hover {
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-box-shadow: 0px 0px 10px #444;
            box-shadow: 0px 0px 10px #444;
}

.SmurfCard img{
    width: 100%;
}


